.actions {
  padding: 120px 0 120px;


  @include vp-1365 {
    padding: 100px 0 100px;
  }

  @include vp-767 {

  }
}

.actions__main-title {
  margin-bottom: 70px;
  font-family: $ff-title;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  text-align: center;

  @include vp-1365 {
    margin-bottom: 40px;
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    letter-spacing: 0.96px;
  }
}

.actions__wrapper {
  display: flex;
  gap: 40px;

  @include vp-1365 {
    flex-direction: column;
    gap: 30px;
  }

  @include vp-767 {

  }
}

.actions__info {
  display: flex;
  flex-wrap: wrap;

  @include vp-1365 {

  }

  @include vp-767 {

  }
}

.actions__text-block {
  display: flex;
  flex-direction: column;
  padding: 40px 45px 49px;
  color: $color-default-white;
  background-color: $color-shine-red;

  @include vp-1365 {
    padding: 45px 44px 49px 45px;
  }

  @include vp-767 {

  }
}

.actions__title {
  margin-bottom: 15px;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;

  @include vp-1365 {
    margin-bottom: 5px;
    font-size: 24px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }
}

.actions__text {
  margin-bottom: 25px;
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;

  @include vp-1365 {
    margin-bottom: 20px;
  }

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
  }
}

.actions__description {
  width: 190px;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;

  @include vp-1365 {

  }

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
  }
}

.actions__link {
  width: min-content;
  position: relative;
  margin-top: auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: inherit;

  @include vp-1365 {

  }

  @include vp-767 {

  }

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    bottom: -4px;
    left: 0;
    background-color: $color-default-white;

    @include vp-1365 {

    }

    @include vp-767 {

    }
  }
}

.actions__list {
  flex-grow: 1;

  @include vp-1365 {
flex-grow: 0;
  }

  @include vp-767 {

  }
}
